import { createRouter, createWebHashHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView,
    meta: {
      title: "布公会客厅"
    }
  },
  {
    path: '/brollo',
    name: "brollo",
    component: () => import("../views/BrolloView"),
    meta: {
      title: "布洛洛的实验室"
    }
  },
  {
    path: '/teaparty',
    name: 'teaparty',
    component: () => import("../views/TeapartyView"),
    meta: {
      title: "茶话会"
    }
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue'),
    meta: {
      title: "关于本站"
    }
  },
  {
    path: '/devlog',
    name: 'devlog',
    component: () => import("../views/DevlogView"),
    children: [
      {
        path: '2.0',
        component: () => import("../components/devlog_2_0.vue")
      }
    ],
    meta: {
      title: "开发日志"
    }
  },
  {
    path: "/other",
    name: "other",
    children: [
      {
        path: "lunar77",
        component: () => import("../components/Lunar_77.vue"),
        meta: {
          title: "七夕节，鹊桥会"
        }
      }
    ]
  },
  {
    path: '/:pathMatch(.*)*',
    name: 'notfound',
    component: () => import("../views/NotFound"),
    meta: {
      title: "此路径不存在"
    }
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

router.beforeEach((to, from, next) => {  
  // 修改页面标题  
  if (to.meta && to.meta.title) {  
    document.title = to.meta.title;  
  }  
  next();  
}); 

export default router
