<template>
  <div class="week-schedule-container">
    <div class="week-schedule semi-space-block" v-for="(day, index) in days" :key="index"
      :class="{ 'today-border': isToday(index) }">
      <div class="day-name">{{ day.name }}</div>
      <div class="events-container">
        <div v-if="day.events.length > 0">
          <div v-for="(event, eventIndex) in day.events" :key="index - eventIndex" class="event">
            {{ event }}
          </div>
        </div>
        <div v-else class="no-live-message">
          {{ getRandomNoLiveMessage() }}
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref } from 'vue';
import schedule from '@/assets/schedule.json'

// 预设的几个没有直播的句子  
const noLiveMessages = [
  'sudo 电脑自己打代码',
  '哦~跑代码如同月亮阴晴不定般变化多端……',
  '别一直盯着周表看啦！你真的没什么事做吗？',
  '有一个人前来打代码……',
  '酒吧里为什么不能点炒饭？',
  '都说了1024这种才是整数……',
  'No Signal',
  '-1 = 4294967295',
  '4004: Never Found',
  '你Debug，就一定Debug了吧！',
  '“万圣节 = 圣诞节”',
];

// 随机选择一个没有直播的句子  
function getRandomNoLiveMessage() {
  return noLiveMessages[Math.floor(Math.random() * noLiveMessages.length)];
}

// 定义星期和直播安排的数据  
const days = ref(schedule);

const today = new Date().getDay();
function isToday(index) {
  return (index + 1) === today;
}
</script>

<style scoped>
.week-schedule-container {
  display: flex;
  justify-content: center;
  /* 横向居中 */
  flex-wrap: wrap;
  /* 如果需要，允许内容换行 */
  margin-top: 0.75rem;
  margin-left: 30rem;
  margin-right: 30rem;
  border-radius: 10px;
  background-color: #def;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.25);
}

.week-schedule {
  font-family: Consolas, 黑体, sans-serif;
  font-size: 1.25rem;
  display: flex;
  align-items: center;
  /* 垂直居中 */
  justify-content: space-between;
  /* 两列之间有空隙 */
  width: 100%;
  /* 或者根据需要设定一个最大宽度 */
}

.day-name {
  flex: 0 0 100px;
  /* 固定宽度或根据需求调整 */
  font-weight: bold;
}

.events-container {
  flex: 1;
  /* 弹性宽度，占据剩余空间 */
  display: flex;
  flex-direction: column;
  /* 直播事件垂直排列 */
  align-items: flex-start;
  /* 如果需要，可以调整 */
}

.event {
  font-weight: bold;
}

.no-live-message {
  color: #666;
  /* 灰色字体，表示没有直播安排 */
}

.today-border {
  border: 2px solid #2c3e50;
  border-radius: 10px;
}
</style>